let timer = null
export default {
	onLoad () {
		
		this.userPhone = uni.getStorageSync('user_phone')
	},
	data(){
		return{
			// 验证码的个数（现在是6个）
			list: [
				{val: '', fs: true},  // 初始发时使第一个输入框架得到焦点
				{val: '', fs: false}, 
				{val: '', fs: false},
				{val: '', fs: false},
				{val: '', fs: false},
				{val: '', fs: false}
			],
			verificationFlag:false,
			count:60,
			inputContent:'',
			userPhone: ''
		}
	},
	props:['show', 'phone'],
	methods:{
		//当输入框的内容改变时的操作
		onInput(e) {
			const l = e.target.value.length;
			for(let i = 0; i < this.list.length; i++) {
				this.list[i].fs = false;
				this.list[i].val =  e.target.value[i];
			};
			if(l) this.list[l - 1].fs = true;
		},
		// 提示验证码方法
		onSubmit() {
			let code = '';
			this.list.forEach(item =>{
				code += item.val
			})
			if(code && code.length == this.list.length){
				this.$emit('code',code)
				clearInterval(timer)
				this.count = 60
			}else{
				uni.showToast({
					icon:'none',
					title:'请输入完整的验证码'
				})
			}
		},
		// 关闭
		handleClose(){
			this.list = [
				{val: '', fs: true},{val: '', fs: false},{val: '', fs: false},
				{val: '', fs: false},{val: '', fs: false},{val: '', fs: false},
			]
			clearInterval(timer)
			this.count = 60
			this.$emit('close',false)
		},
		// 发送验证码
		sendVerification(){
			this.verificationFlag = true
			timer = setInterval(() =>{
				if(this.count == 0){
					clearInterval(timer)
					this.verificationFlag = false
					this.count = 60
				}
				this.count = --this.count
			},1000)
		}
	},
	watch:{
		show(){
			if(this.show){
				this.sendVerification()
				this.list = [
					{val: '', fs: true},{val: '', fs: false},{val: '', fs: false},
					{val: '', fs: false},{val: '', fs: false},{val: '', fs: false},
				],
				this.inputContent = ''
			}
		}
	}
}
